a {
	text-decoration: underline;
	color: $gray-600;
	&:hover {
		color: $indigo;
	}
	&.text-primary {
		color: $indigo;
		&:hover {
			color: $purple;
		}

	}
}

b, strong {
    font-weight: $font-weight-bold;
}

small,
.small {
    font-size: 10px;
}

.text-md {
    font-size: 16px;
}
.text-lg {
    font-size: 18px;
}

.lead {
    font-size: 24px;
    font-weight: $font-weight-bolder;
    //color: $gray-600;
}

.line-height-0 {
    line-height: 0;
}
.line-height-1 {
    line-height: 1;
}

.text-clear {
    text-decoration: none;
}
