#app {
	padding-top: 5rem;
    @include media-breakpoint-down(sm) {
        padding-top: 50px;
    }
}

html, body {
	font-family: 'Circe', sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: 14px;
}

svg {
	@extend .ease;
	&.icon {
		width: 18px;
		height: 18px;
        @include media-breakpoint-down(sm) {
            width: 13px;
            height: 13px;
        }
	}
    &.icon-lg {
        width: 25px;
        height: 25px;
    }
}

svg.text-gray,
.text-gray svg {
    fill: $gray;
    &:hover {
        fill: $primary;
    }
}

section {
    padding-top: 4.5rem;
    padding-bottom: 7.5rem;
}

#section-home-search {
    @include media-breakpoint-down(sm) {
        padding-bottom: 10.5rem;
    }
    h1 {
        font-size: 44px;
    }
    .card {
        box-shadow: $box-shadow-lg;
    }
    .card-body {
        padding: 0.5rem;
    }
    .form-control {
        background-color: transparent;
        border-width: 0;
        box-shadow: none;
    }
    .submit-container {
        @include media-breakpoint-down(sm) {
            position: absolute;
            top: 100%;
            margin-top: $font-size-base;
        }
    }
}

.section-hr {
    position: relative;
    height: 6px;
    background-color: $white;
    .btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        box-shadow: $box-shadow-lg;
        padding: 0;
        left: 50%;
        margin-left: -25px;
        top: -22px;
    }
}

.delivery-item {
    .delivery-map {
        height: 0;
        border-radius: $border-radius;
        overflow: hidden;
    }
    &.map-opened {
        .show-map {
            i {
                transform: rotate(180deg);
            }
        }
        .delivery-map {
            height: auto;
        }
    }
    .circle {
        background-color: $white;
        width: 26px;
        height: 26px;
        border: solid 2px $gray-300;
        border-radius: 50%;
        i {
            display: block;
            background-color: $primary;
            width: 18px;
            height: 18px;
            border-radius: 50%;
            margin: 2px;
        }
    }
    .delivery-data {
        &:before {
            display: block;
            content: '';
            position: absolute;
            width: 2px;
            top: 0;
            left: calc(#{$grid-gutter-width} - 2px);
            height: 100%;
            background-color: $gray-300;
        }
    }
    &:first-child {
        .delivery-data {
            &:before {
                top: 50%;
                height: 50%;
            }
        }
    }
    &:last-child {
        .delivery-data {
            &:before {
                height: 50%;
            }
        }
    }
    @include media-breakpoint-down(sm) {
        .delivery-dates {
            padding-left: 54px;
        }
        &:not(:first-child) {
            .delivery-dates {
                padding-top: 1.25rem;
                &:before {
                    display: block;
                    content: '';
                    position: absolute;
                    width: 2px;
                    top: 0;
                    left: calc(#{$grid-gutter-width} - 2px);
                    height: 100%;
                    background-color: $gray-300;
                }
            }
        }
        .delivery-date {
            font-size: 11px;
        }
        .delivery-year {
            font-size: 10px;
        }
        .delivery-timeout {
            font-size: 10px;
        }
        .label {
            font-size: 13px;
        }
        .show-map {
            font-size: 10px;
        }
        .delivery-type {
            font-size: 10px;
        }
    }
}
